import Vue from 'vue';
import Router from 'vue-router';
import HelloWorld from '@/components/HomePage';
import Asset from '@/components/Asset';
import VueHotkey from 'v-hotkey';

Vue.use(Router);
Vue.use(VueHotkey);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HomePage',
      component: HelloWorld
    },
    {
      path: '/assets/:id',
      name: 'Asset',
      component: Asset
    }
  ]
});
