<template>
    <div>
      <EdgeHeader color="teal darken-1"/>
      <div class="container free-bird">
        <row v-for="asset in assets">
          <column md="10" class="mx-auto float-none white z-depth-1 py-2 px-2">
            <card-body>

              <router-link :to="{ name: 'Asset', params: { id: asset.entityRef.tag }}">
              <a>
                <h2 class="h2-responsive pb-4"><strong>{{ asset.name }}</strong></h2>
              </a>
              </router-link>
              <div>
                <b>URI: </b>{{ asset.entityRef.uri }}
              </div>
              <div>
                <b>Version URI: </b>{{ asset.entityRef.versionId }}
              </div>
              <div>
                <b>Tag: </b>{{ asset.entityRef.tag }}
              </div>
              <div>
                <b>Version: </b>{{ asset.entityRef.version }}
              </div>

            </card-body>
          </column>
        </row>
      </div>
    </div>
</template>

<script>
import { Container, Column, Row, Fa, Navbar, NavbarItem, NavbarNav, NavbarCollapse, Btn, EdgeHeader, CardBody } from 'mdbvue';
import axios from 'axios';

export default {
  name: 'HomePage',
  components: {
    Container,
    Column,
    Row,
    Fa,
    Navbar,
    NavbarItem,
    NavbarNav,
    NavbarCollapse,
    Btn,
    EdgeHeader,
    CardBody
  },
  created: function () {
    this.getAssets()
  },
  data() {
    return {
      assets: []
    }
  },
  methods: {
    getAssets: function () {
      axios.get('/cat/assets')
          .then((assets) => this.assets = assets.data)
      .catch(e => {
          throw e;
      })
    }
  }
};
</script>

